<template>
  <div id="app">
    <!-- <el-select v-model='mytheme' @change="handleChange">
      <el-option key="light" label="Light Mode" value="light"></el-option>
      <el-option key="dark" label="Dark Mode" value="dark"></el-option>
    </el-select> -->
    <Home/>
 
  </div>
</template>

<script>
import DarkTheme from "./components/DarkTheme";
import LightTheme from "./components/LightTheme";
import Home from './components/Home'
export default {
  name: "App",
  props: ["theme"],
  data() {
    return {
      mytheme: 'light'
    };
  },
  watch:{
    theme(newval,oldval){
      console.log('theme changes')
      this.mythme = newval;
    }
  },
  methods:{
    handleChange(){
      console.log('calling handleChange')
      window.sessionStorage.setItem('theme',this.mytheme)
      console.log('itemset')
      window.location.reload()
    }
  },
  components: {
  
    Home
  },
  created(){
    this.mytheme = this.theme;
  }
};
</script>

<style lang='css'>
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



